import Note from './su-note' 
import { connect } from 'react-redux'

const mapStateToProps = (state) => {
  return {
    activeEditor: state.note.activeEditor,
    activeEditorIndex: state.note.activeEditorIndex,
    noteItems: state.note.notes,
    showEditor: state.note.showEditor,
    noedit: (state.mdEditor.currentEditorId && !state.mdEditor.editors[state.mdEditor.currentEditorId].edit) || false,
    sidebarVisible: state.note.sidebarVisible,
    editNoteItems: state.notebook.noteMeta.chapters,
    noteId: state.notebook.noteMeta.noteId,
    noNoteExists: state.notebook.noNoteExists,
    authorized: state.notebook.authorized,
 }
}

const mapDispatchToProps = (dispatch, action) => {
  return {
    setNotebookState: (payload) => {
      dispatch({
        type: 'NOTEBOOK_SET_STATE',
        payload
      })
    },
    setActiveEditor: (id, index) => {
      dispatch({
        type: 'SET_ACTIVE_EDITOR',
        id,
        index
      })
    },
    setActiveLink: (id, index) => {
      dispatch({
        type: 'SET_ACTIVE_LINK',
        id,
        index
      })
    },
    setPreviousActive: () => {
      dispatch({
        type: 'SET_PREVIOUS_ACTIVE'
      })
    },
    setNextActive: () => {
      dispatch({
        type: 'SET_NEXT_ACTIVE'
      })
    },
    createNote: (id, env) => {
      dispatch({
        type: 'CREATE_NOTE',
        id,
        env
      })
    },
    createLocalNotesForView: (cloudData) => {
      dispatch({
        type: 'CREATE_LOCAL_NOTES_FOR_VIEW',
        cloudData
      })
    }
  }
}

const VisibleNote = connect(
  mapStateToProps,
  mapDispatchToProps
)(Note)

export default VisibleNote
















