import store from './../../store'
import NoteBook from './../../../gapi/notebook'
import Note from './../../../gapi/note'

let suNote = suNote || {}
let suNoteNook = suNoteNook || {}

window.NoteBook = NoteBook
window.Note = Note

const note = (state, action) => {
  // TODO: dont create new object every time
  var obj = {
    noteBookMeta: {},
    noteMeta: {},
    noNoteExists: false,
    authorized: false
  }
  var noteBook = {},
      note = {},
      payload = {},
      editEl = {};

  switch(action.type){
    case 'CREATE_NOTEBOOK':
      noteBook = new NoteBook(action.id)
      noteBook.init((err, metadata)=>{
        if (err) {
          console.error(err)
          return
        }
        suNoteNook =  noteBook
        payload = {
          noteBookMeta: metadata
        }
        store.dispatch({
          type: 'NOTEBOOK_SET_STATE',
          payload
        })
      })
      return Object.assign({}, state, {})

    case 'CREATE_NOTE':
      if (window.env !== 'dev') {
        if (state.noteMeta.noteId === action.id) {
          console.log('alaska!')
          return state
        }
        
        suNote = new Note(action.id)
        // console.log(action.id)
        suNote.init && suNote.init((err, metadata)=>{
          if (err || (metadata.error && metadata.error.code)) {
            payload = {
              noNoteExists: true
            }
            console.error('Error:notebook: Note does not exists.', err)

            store.dispatch({
              type: 'NOTEBOOK_SET_STATE',
              payload
            })
            return
          }

          metadata.noteId = action.id
          // console.log(metadata)

          payload = {
            noteMeta: metadata,
            noNoteExists: false
          }
          store.dispatch({
            type: 'NOTEBOOK_SET_STATE',
            payload
          })
        })
      }

      return Object.assign({}, state, {})

    case 'NOTEBOOK_SET_STATE':
      return Object.assign({}, state, action.payload)

    case '@SERVER_COMMIT_EL_NAME':
      editEl = action.editEl
      
      console.log(editEl, suNote.noteId)

      if (window.env !== 'dev') { 
        if (editEl.remoteId) {
        } else {
          if (editEl.type === 'topic') {
            suNote.createTopic(editEl.parentId, editEl.name, (res)=>{
              console.log(res)
            })
          } else if (editEl.type === 'chapter') {
            suNote.createChapter(editEl.name, (res)=>{
              console.log(res)
            })
          }
        }
      } // gapi

      return Object.assign({}, state, action.payload)

      return Object.assign({}, state, action.payload)

    case 'SAVE_NOTE_CONTENT':
      console.log(action.id, action.markdownText)
      // updating
      // updated
      return state

    default:
      return state || obj
  }
}

export default note;


