const editorStore = (state, action) => {
  var obj = {
    editors: {},
    currentEditorId: ''
  }

  var editors = {}
  var currentEditor = {}

  switch(action.type){    
    case 'INIT_EDITOR_FROM_ID':
      if (state.editors[action.id]) {
        return Object.assign({}, state, {
          currentEditorId: action.id
        })
      }

      editors = Object.assign({}, state.editors, {
        [action.id]: {
          id: action.id,
          edit: false,             // determines if editor needs to be rendered
          preview: true,        // edit mode or preview mod
          dirty: false,
          editing: false,
          edited: false,
          htmlText: '<h2>Write something amazing</h2>',
          markdownText: '## Write something amazing'
        }
      })
      return Object.assign({}, state, {
        editors,
        currentEditorId: action.id
      })

    case 'SET_EDITOR_STATE':
      currentEditor = state.editors[state.currentEditorId]
      currentEditor = Object.assign({}, currentEditor, action.payload)
      editors = Object.assign({}, state.editors, {
        [state.currentEditorId]: currentEditor
      })

      return Object.assign({}, state, {
        editors
      })

    default:
      return state || obj
  }
}
export default editorStore;


