import Note from './index'
import { connect } from 'react-redux'

const mapStateToProps = (state) => {
  return {
    notes: state.notebook.noteBookMeta.notes || []
  }
}

const mapDispatchToProps = (dispatch, action) => {
  return {
    createNoteBook: (res) => {
      dispatch({
        type: 'CREATE_NOTEBOOK',
        id: res.id
      })
    },
    setNextActive: () => {
      dispatch({
        type: 'R_SET_NEXT_ACTIVE'
      })
    },
    toggleSidebar: () => {
      dispatch({
        type: 'R_TOGGLE_SIDEBAR'
      })
    },
    setNoteExists: () => {
      var payload = {
        noNoteExists: false
      }
      dispatch({
        type: 'NOTEBOOK_SET_STATE',
        payload
      })
    }
  }
}

const VisibleNote = connect(
  mapStateToProps,
  mapDispatchToProps
)(Note)

export default VisibleNote


