import Chance from 'chance';
// import store from './../../store'
import { addChapter, removeChapter, addTopic, removeTopic, toggleDisabled } from './note.fn'
import { onMouseMove, onMouseUp } from './note.mouse.fn'
import { chapterContextMenu, topicContextMenu } from './sidebar-context-menu.fn'

var chance = new Chance();
var poolStr = 'abcdefghijklmnopqrstuvwxyz0123456789'
const note = (state, action) => {
  console.log(action.type)
  // TODO: dont create new object every time
  var obj = {
    notes: [],
    // notes: [
    //   {
    //     id: 'sadasdasdasdasdasd1',
    //     name: 'New Chapter 1',
    //     edit: false,
    //     selected: true,
    //     type: 'chapter',
    //     children: 0,
    //     gIndex: 0,
    //     disabled: false,
    //     icon: '---'
    //   },
    //   {
    //     id: 'sadasdasdasdasdasd2',
    //     name: 'New Chapter 2',
    //     edit: false,
    //     selected: false,
    //     type: 'chapter',
    //     children: 0,
    //     gIndex: 1
    //   },
    //   {
    //     id: 'sadasdasdasdasdasd3',
    //     name: 'New Chapter 3',
    //     edit: false,
    //     selected: false,
    //     type: 'chapter',
    //     children: 0,
    //     gIndex: 2
    //   }
    // ],
    activeEditor: -1,
    activeEditorIndex: -1,

    // sidebar content edit state
    editElIndex: 'sadasdasdasdasdasd1',//-1,
    editing: false,
    showEditor: false, // THIS IS SET WHEN EDITOR IS LOADED FROM CLOUD

    // sidebar content dragging state
    mousedown: false,
    elId: -1,
    el: '',
    dropIndex: -1,
    dropEl: '',
    elChildren: [],

    // sidebar content context menu state
    showContextMenu: false,
    contextMenuStyle: {},
    ctxTargetEl: {},
    contextItems: [],

    sidebarVisible: true
  }

  var notes = {},
      editElIndex = -1,
      newNoteId = '',
      el = {},
      newState = {},
      contextItems = [],
      targetEl = '',
      index = -1,
      start = -1,
      end = -1,
      chapters = [],
      path = '',
      name = '';

  switch(action.type){
    case 'SET_ACTIVE_EDITOR':
      return Object.assign({}, state, {
        activeEditor: action.id,
        activeEditorIndex: action.index,
        showEditor: true
      })

    case 'SET_PREVIOUS_ACTIVE':
      // get prev id
      var prevId = state.activeEditor
      notes = state.notes
      index = -1
      for (start = 0, end = notes.length; start < end; start++) {
        if (notes[start].id === state.activeEditor) {
          if (start === 0) {
            prevId = notes[0].id
            index = 0
          } else {
            prevId = notes[start-1].id
            index = start-1
          }
          break
        }
      }

      notes = notes.map((_el) => {
        _el.selected = (_el.id === prevId)
        return _el
      })

      return Object.assign({}, state, {
        notes,
        activeEditor: prevId,
        activeEditorIndex: index
      })

    case 'SET_NEXT_ACTIVE':
      // get prev id
      var nextId = state.activeEditor
      notes = state.notes
      index = -1
      for (start = 0, end = notes.length; start < end; start++) {
        if (notes[start].id === state.activeEditor) {
          if (start === end-1) {
            nextId = notes[end-1].id
            index = end-1
          } else {
            nextId = notes[start+1].id
            index = start+1
          }
          break
        }
      }

      notes = notes.map((_el) => {
        _el.selected = (_el.id === nextId)
        return _el
      })

      return Object.assign({}, state, {
        notes,
        activeEditor: nextId,
        activeEditorIndex: index
      })

    case 'SET_ACTIVE_LINK':
      notes = state.notes.map((_el) => {
        _el.selected = (_el.id === action.id)
        return _el
      })

      return Object.assign({}, state, {
        notes,
        activeEditorIndex: action.index
      })

    case 'SET_EDIT_EL':
      notes = [].concat(state.notes)
      if (action.index !== -1) {
        notes[action.index].edit = true
      }
      return Object.assign({}, state, {
        editElIndex: action.index,
        notes
      })

    case 'SET_EDITING':
      return Object.assign({}, state, {
        editing: action.flag
      })

    case 'EDIT_EL_NAME':
      notes = state.notes.map((_el) => { 
        _el.selected = false
        return _el
      })
      notes[state.editElIndex].name = action.name
      return Object.assign({}, state, { notes })
      
    case 'COMMIT_EL_NAME':
      notes = [].concat(state.notes)
      el = notes[state.editElIndex]
      el.name = el.name.trim(' ')
      el.edit = false
      el.selected = true
      return Object.assign({}, state, { notes })

    case 'ADD_NEW_CHAPTER':
      newNoteId = chance.string({length: 32, pool: poolStr})
      notes = addChapter(state.notes, newNoteId)

      editElIndex = notes.length - 1

      // store.dispatch
      path = `/note/edit/${action.noteId}/New-Chapter/${editElIndex}`

      return Object.assign({}, state, {
        notes,                                 // update notes
        editElIndex,                          // used in ContentList.onBlur
        editing: true,                       // make the editor change the mode
        showEditor: true,
        activeEditor: newNoteId,
        activeEditorIndex: editElIndex,
        to: path
      })

    case 'ADD_NEW_TOPIC':
      newNoteId = chance.string({length: 32, pool: poolStr});
      obj = addTopic(state.notes, newNoteId, action.index)

      path = `/note/edit/${action.noteId}/New-Topic/${obj.addTopicIndex}`

      return Object.assign({}, state, {
        notes: obj.notes,                         // update notes
        editElIndex: obj.addTopicIndex,          // used in ContentList.onBlur
        editing: true,                          // make the editor change the mode
        showEditor: true,
        activeEditor: newNoteId,
        activeEditorIndex: obj.addTopicIndex,
        to: path
      })

    case 'ON_MOUSE_DOWN':
      return Object.assign({}, state, {
        elId: action.el.id,
        mousedown: true
      })

    case 'ON_MOUSE_MOVE':
      newState = onMouseMove(state, action.el, action.event)
      return newState

    case 'ON_MOUSE_UP':
      newState = onMouseUp(state)
      return Object.assign({}, newState)

    case 'HIDE_CONTENT_CONTEXT_MENU_STYLE':
      return Object.assign({}, state, {
        showContextMenu: false
      })

    case 'SHOW_CONTENT_CONTEXT_MENU_STYLE':
      // console.log(action.targetEl)
      // process action.targetEl and create appropriate context menu
      targetEl = action.targetEl
      if (targetEl.type === 'chapter') {
        contextItems = chapterContextMenu
      } else {
        contextItems = topicContextMenu
      }

      contextItems[1].nameIndex = targetEl.disabled ? 1:0     // for make disabled/enabled
      contextItems[3].nameIndex = targetEl.icon ? 1:0        // for add icon/ remove icon

      return Object.assign({}, state, {
        contextItems,
        ctxTargetEl: action.targetEl,
        showContextMenu: true,
        contextMenuStyle: {
          left: action.event.nativeEvent.clientX + 'px',
          top: action.event.nativeEvent.clientY + 'px'
        }
      })

    case 'TOGGLE_DISABLED':
      notes = toggleDisabled(state.notes, state.ctxTargetEl.gIndex)
      return Object.assign({}, state, {
        notes,
        showContextMenu: false  
      })

    case 'REMOVE_TOPIC':
      notes = removeTopic(state.notes, state.ctxTargetEl.gIndex)
      
      return Object.assign({}, state, { 
        notes, 
        showEditor: notes.length,
        showContextMenu: false
      })

    case 'REMOVE_CHAPTER':
      notes = removeChapter(state.notes, state.ctxTargetEl.gIndex, state.ctxTargetEl.children)
      return Object.assign({}, state, { 
        notes,
        showEditor: notes.length,
        showContextMenu: false  
      })

    case 'TOGGLE_SIDEBAR':
      return Object.assign({}, state, { 
        sidebarVisible: !state.sidebarVisible
      })

    case 'CREATE_LOCAL_NOTES_FOR_VIEW':
      chapters = action.cloudData
      notes = []

      chapters.forEach((_el)=>{
        name = _el.name
        index = name.lastIndexOf('.')
        if (index != -1) {
          name = name.slice(0, index)
        }
        notes.push({
          name: name,
          id: chance.string({length: 32, pool: poolStr}),
          remoteId: _el.id,
          children: _el.topics.length,
          type: 'chapter',
          edit: false,
          selected: false
        })
        for (var i = _el.topics.length - 1; i >= 0; i--) {
          var el = _el.topics[i] 
          name = el.name
          index = name.lastIndexOf('.')
          if (index != -1) {
            name = name.slice(0, index)
          }
          notes.push({
            name: name,
            id: chance.string({length: 32, pool: poolStr}),
            remoteId: el.id,
            type: 'topic',
            edit: false,
            selected: false
          })
        }
      })

      notes.forEach((_el, _index) => { _el.gIndex = _index })
      return Object.assign({}, state, {
        notes
      })

    default:
      return state || obj
  }
}

export default note;


